import React, { Component } from 'react'
import { Card, CardImg, CardText, CardBody, Button, CardTitle, CardLink } from 'reactstrap'
import './App.css'

class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      name: '',
      description: '',
      feed: [],
      requestFailed: false
    }
  }

  componentDidMount () {
    fetch(`https://graph.facebook.com/v2.9/1758792264412132?fields=name,description,feed.limit(5){from,story,message,permalink_url,link,created_time,full_picture,comments,reactions{type}}&access_token=EAACEdEose0cBAAGRsfZAC5DJ90GIZAfRgcjW9hTlIsjJfg98deiOVu0aDOg0hXWw8Ry3O4ZACq1zN1U9KIBE4zEPHNRNf6ZAqi2gUuSiqhc2nJh3m6foaw1tbIwyfpUDJ21jkJ1u4eiJH2GCuaYVmHEvNuHDdNZAOW459xKqUWXcTrymzPL9Eq2YN9doXyucgRnQtPZCBZAiAZDZD`)
      .then(console.log('Fetch successful'))
      .then(response => {
        if (!response.ok) {
          throw Error('Error fetching API!')
        }
        return response
      })
      .then(data => data.json())
      .then(data => {
        var reactionCount = [], commentCount = [], posts = []

        for (var i = 0; i < data.feed.data.length; i++) {

          (!data.feed.data[i].reactions) ? reactionCount.push(0) : reactionCount.push(data.feed.data[i].reactions.data.length)

          if(!data.feed.data[i].comments) {
            commentCount.push(0)
          } else {
            commentCount.push(data.feed.data[i].comments.data.length)
          }

          posts.push(
            <Card>
              <CardBody>
                <CardTitle>{data.feed.data[i].from.name}</CardTitle>
                <CardText>{data.feed.data[i].created_time.substring(0, 10)} at {data.feed.data[i].created_time.substring(11, 16)}</CardText>
                <CardText>{data.feed.data[i].message.replace(/\n/g, "\n")}</CardText>
              </CardBody>
              <CardImg src={data.feed.data[i].full_picture} />
              <CardBody>
                <CardLink href={data.feed.data[i].link}>Link</CardLink>
                <meta property="og:url"                content={data.feed.data[i].link} />
                <br /><br />
                <Button><a href={data.feed.data[i].permalink_url}>{reactionCount[i]} reactions</a></Button> <Button><a href={data.feed.data[i].permalink_url}>{commentCount[i]} comments</a></Button>
              </CardBody>
            </Card>
          )
        }
        this.setState({
          name: data.name,
          description: data.description,
          feed: posts
        })
        console.log(data);
      }, () => {
        this.setState({
          requestFailed: true
        })
      })
  }

  render () {
    return (
      <div className='container'>
        <div className='head'>
          <p id='title'>{this.state.name}</p>
          <p id='about'>{this.state.description}</p>
        </div>
        <div className='body'>
          <div>{this.state.feed}</div>
        </div>
      </div>
    )
  }
}

export default App
